@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap);


#load{
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  position: fixed;
  background-color:#fbc178;
  z-index: 10000;
}

body{
  background-color: #010324;
  color: aliceblue;
  padding: 0%;
  position: relative;
  font-family: Poppins, sans-serif !important;
}
.gif{
  width: 100%;
  height: 100%;
}
.navbar{
  background-color: #00043a !important;
}
.btn-primary{
  background-color: #fbc178;
  color: #00043a;
}
.navbar-brand img{
  width: 50px;
  height: 50px;
}
.banner-img{
  display: block;
  width: 100%;
}
.outline{
  border: solid #ffffff 1px;
  border-radius: 10px;
  padding: 20px;
}
.icon{
  display: block;
  margin: auto;
  text-align: center;
  width: 50px;
  height: 50px;
}
.icon i{
  display: block;
  padding: 15px 5px;
  border-radius: 50%;
  color: aliceblue;
  background-color:#ff4900;
}
.road-list li{
  background-color: #1117817a;
  padding: 15px 10px;
  margin-bottom: 10px;
  list-style-type: none;
  border-left: #fbc178 solid 2px;
}
.hlink{
  color: #fbc178;
  text-decoration: none;
  font-weight: 700;
}
.buy-list li{
  background-color: #92808041;
  list-style-type: none;
  margin-bottom: 10px;
  border-radius: 20px;
  padding: 10px 10px;
}
.wrap{
  overflow-wrap: break-word;
}
.social{
  color: #ffffff;
  background-color: #2a36d85e;
}
.social:hover{
  background: #ffffff85;
  font-weight: bolder;
  color: #2934cc;
}
#myChart{
  width: 200px;
  height: 200px;
}
.token *{
  color: #ffffff;
}
.overflow-hidden{
  overflow: hidden;
}